.login_logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login_container>h1{
    margin-block-start: 0;
    margin-block-end: 0;
}


.login_container>form>h5{
    margin-block-start: 0;
    margin-block-end: 0;
}

.login_container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login_container>h1{
    font-weight: 500;
    /* margin-bottom: 20px; */
}

.login_container>form>h5{
    margin-bottom: 5px;
}
.login_container>form>input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container>p{
    font-size: 12px;
    margin-top: 15px;
}

.login_signinbutton{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px; 
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login_registerbutton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}

